import React from "react"
import s from "./Header.module.scss"
import Nav from "../Nav/Nav";


let Header = () => {

    return (
        <div className={s.header}>
            <a className={s.navbarBrand} href="#">
                <span className={s.primaryColor}>Fri</span>
                end
            </a>
            <Nav/>
        </div>
    )
}

export default Header;